<template>
  <div class="checkout">
    <h1>CHECKOUT</h1>
    <div class="checkout__container">
      <!-- login -->
      <transition name="fade">
        <div id="checkout__login" class="checkout__block" v-if="user.id === ''">
          <p class="checkout__title checkout__title--collapse"
          :class="loginCollapse ? null : 'collapsed'"
          @click.prevent="loginCollapse = !loginCollapse">
          <span>
            登入 / 註冊會員
          </span>
          </p>
          <b-collapse v-model="loginCollapse">
            <div class="checkout__body">
              <login></login>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- cart confirming -->
      <transition name="fade">
        <div class="checkout__block" v-if="!cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="cartConfirmingCollapse ? null : 'collapsed'"
          @click.prevent="cartConfirmingCollapse = !cartConfirmingCollapse">
          <span>
            確認購物車內容
          </span>
          </p>
          <b-collapse v-model="cartConfirmingCollapse">
            <div class="checkout__body">
              <cart-content :editable='true' :connectObj='cart'></cart-content>
            </div>
            <div class="checkout__func">
              <a href="#" class="myBtn myBtn--primary"
              @click.prevent='confirm_cartContent'>下一步</a>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- cart confirmed -->
      <transition name="fade">
        <div class="checkout__block" v-if="cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="cartConfirmedCollapse ? null : 'collapsed'"
          @click.prevent="cartConfirmedCollapse = !cartConfirmedCollapse">
          <span>
            確認訂購產品
          </span>
          </p>
          <b-collapse v-model="cartConfirmedCollapse">
            <div class="checkout__body">
              <cart-content :editable='false' :connectObj='cart'></cart-content>
            </div>
            <div class="checkout__func">
              <a href="#" class="myBtn myBtn--primary"
              @click.prevent='cancel_confirm_cartContent'>返回編輯購物車</a>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- sum -->
      <transition name="fade">
        <div class="checkout__block" v-if="cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="sumCollapse ? null : 'collapsed'"
          @click.prevent="sumCollapse = !sumCollapse">
          <span>
            金額總計
          </span>
          </p>
          <b-collapse v-model="sumCollapse">
            <div class="checkout__body">
              <div class="checkout__coupon">
                <div class="checkout__coupon__input myForm">
                  <span>使用優惠券: </span>
                  <input type="text" v-model="newOrder.coupon.id">
                  <a href="#" class="myBtn myBtn--secondary"
                  @click.prevent='find_coupon'>使用</a>
                  <a href="#" class="myBtn myBtn--secondary"
                  v-if="newOrder.coupon.description !== ''"
                  @click.prevent='clear_coupon'>清空優惠券</a>
                </div>
                <div class="checkout__coupon__lookup">
                  <a href="#" class="myBtn myBtn--info" v-if="user.id !== ''"
                  >查詢優惠券</a>
                  <a href="#" class="myBtn myBtn--info" v-if="user.id === ''"
                  @click.prevent="scroll_to_login">登入 / 加入會員查詢優惠券</a>
                </div>
              </div>
              <p class="checkout__jcfe">金額小計: NT${{cart.sum}}</p>
              <p class="checkout__jcfe">折扣碼 ID:
                <span v-if="newOrder.coupon.id === ''">無</span> {{newOrder.coupon.id}}</p>
              <p class="checkout__jcfe">折扣: {{-1 * newOrder.coupon.discount}}</p>
              <p class="checkout__jcfe">金額總計: {{newOrder.sum}}</p>
            </div>
            <div class="checkout__func">
              <a href="#" class="myBtn myBtn--primary"
              @click.prevent='orderInfoCollapse = true'>下一步</a>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- order info -->
      <transition name="fade">
        <div class="checkout__block" v-if="cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="orderInfoCollapse ? null : 'collapsed'"
          @click.prevent="orderInfoCollapse = !orderInfoCollapse">
          <span>
            訂購人資訊
          </span>
          </p>
          <b-collapse v-model="orderInfoCollapse">
            <div class="checkout__body">
              <form class="checkout__form" @submit.prevent='emit_order'>
                <div class="checkout__form__block">
                  <p class="checkout__form__title">購買人</p>
                  <div class="checkout__form__body">
                    <div class="myForm">
                      <div class="myForm__title">姓名</div>
                      <input type="text" v-model="newOrder.buyer.name" required>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">聯絡電話</div>
                      <input type="tel" v-model="newOrder.buyer.tel" required>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">Email</div>
                      <input type="email" v-model="newOrder.buyer.email" required>
                    </div>
                  </div>
                </div>
                <div class="checkout__form__block">
                  <p class="checkout__form__title">發票資料</p>
                  <div class="checkout__form__body">
                    <div class="myForm">
                      <div class="myForm__title">發票類型</div>
                      <select v-model="newOrder.invoice.type" required>
                        <option value="" disabled>請選擇發票類型</option>
                        <option value="personal">個人電子發票</option>
                        <option value="donate">捐贈電子發票</option>
                        <option value="company">公司報帳電子發票</option>
                      </select>
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'personal'">
                      <div class="myForm__title">載具類別</div>
                      <select v-model="newOrder.invoice.carrier"
                      :required="newOrder.invoice.type === 'personal'">
                        <option value="" disabled>請選擇載具類別</option>
                        <option value="no">不使用載具</option>
                        <option value="mobile">手機條碼</option>
                        <option value="natural_person">自然人憑證</option>
                      </select>
                    </div>
                    <div class="myForm"
                    v-if="newOrder.invoice.type === 'personal'
                    && newOrder.invoice.carrier === 'no'">
                      <div class="myForm">
                      <address-input
                      :title='"中獎發票寄送地址"'
                      :target='newOrder.invoice'
                      @emit_address='get_address'></address-input>
                    </div>
                    </div>
                    <div class="myForm"
                    v-if="newOrder.invoice.type === 'personal'
                    && newOrder.invoice.carrier === 'mobile'">
                      <div class="myForm__title">手機載具號碼</div>
                      <input type="text" v-model="newOrder.invoice.mobile_carrier">
                    </div>
                    <div class="myForm"
                    v-if="newOrder.invoice.type === 'personal'
                    && newOrder.invoice.carrier === 'natural_person'">
                      <div class="myForm__title">自然人憑證號碼</div>
                      <input type="text" v-model="newOrder.invoice.natural_person_code">
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'donate'">
                      <div class="myForm__title">捐贈單位</div>
                      <select v-model="newOrder.invoice.org">
                        <option value="1">捐贈單位1</option>
                        <option value="2">捐贈單位2</option>
                        <option value="3">捐贈單位3</option>
                      </select>
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'company'">
                      <div class="myForm__title">買受人</div>
                      <input type="text" v-model="newOrder.invoice.companyTitle">
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'company'">
                      <div class="myForm__title">統一編號</div>
                      <input type="text" v-model="newOrder.invoice.companyTaxId">
                    </div>
                  </div>
                </div>
                <div class="checkout__form__block">
                  <p class="checkout__form__title">收件人資料</p>
                  <div class="checkout__form__body">
                    <div class="myForm">
                      <label class="myForm__checkbox">
                        <input type="checkbox" v-model="newOrder.delivery.get_from_store">
                        <span class="myForm__checkbox__span">
                          門市自行取貨
                        </span>
                      </label>
                    </div>
                    <div class="myForm" v-if="!newOrder.delivery.get_from_store">
                      <div class="myForm__title">姓名</div>
                      <input type="tel" v-model="newOrder.recipient.name"
                      :required='!newOrder.delivery.get_from_store'>
                    </div>
                    <div class="myForm" v-if="!newOrder.delivery.get_from_store">
                      <div class="myForm__title">聯絡電話</div>
                      <input type="tel" v-model="newOrder.recipient.tel"
                      :required='!newOrder.delivery.get_from_store'>
                    </div>
                    <div class="myForm" v-if="!newOrder.delivery.get_from_store">
                      <address-input
                      :title='"地址"'
                      :target='newOrder.recipient'
                      :required='!newOrder.delivery.get_from_store'
                      @emit_address='get_address'></address-input>
                    </div>
                    <div class="myForm" v-if="!newOrder.delivery.get_from_store">
                      <label class="myForm__checkbox">
                        <input type="checkbox" v-model="newOrder.delivery.has_recipient2">
                        <span class="myForm__checkbox__span">
                          公司 / 大樓代收
                        </span>
                      </label>
                    </div>
                    <div class="myForm" v-if="newOrder.delivery.has_recipient2">
                      <div class="myForm__title">收花人公司 / 大樓名稱</div>
                      <input type="text" v-model="newOrder.delivery.recipient2Name">
                    </div>
                    <div class="myForm" v-if="!newOrder.delivery.get_from_store">
                      <label class="myForm__checkbox">
                        <input type="checkbox" v-model="newOrder.delivery.anonymous">
                        <span class="myForm__checkbox__span">
                          匿名配送
                        </span>
                      </label>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">預定到貨 / 取貨日期</div>
                      <input type="date" v-model="newOrder.delivery.esdDate" required>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">希望到貨 / 取貨時段</div>
                      <select v-model="newOrder.delivery.esdTime">
                        <option value="">不指定</option>
                        <option value="morning">上午( 08:00 ~ 13:00 )</option>
                        <option value="afternoon">下午( 14:00 ~ 18:00 )</option>
                      </select>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">卡片內容</div>
                      <textarea v-model='newOrder.others.cardContent'
                      placeholder="若此欄位空白，則不附卡片( 請勿使用手機表情符號 )"></textarea>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">備註</div>
                      <textarea v-model='newOrder.others.remark'
                      placeholder="商品 / 配送 / 其他特殊需求"></textarea>
                    </div>
                  </div>
                </div>
                <div class="checkout__form__block checkout__form__block--submit">
                  <button type="submit" class="myBtn myBtn--primary"
                  >建立訂單，前往付款</button>
                </div>
              </form>
            </div>
          </b-collapse>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import CartContent from '@/components/CartContent.vue';
import Login from '@/components/Login.vue';
import AddressInput from '@/components/AddressInput.vue';

export default {
  components: { Login, CartContent, AddressInput },
  computed: {
    cart() {
      let cart;
      this.$bus.$on('get_cart', (data) => {
        cart = data;
      });
      this.$bus.$emit('call_cart');
      return cart;
    },
    user() {
      let user;
      this.$bus.$on('son_get_data', (data) => {
        user = data.member;
      });
      this.$bus.$emit('son_call_data');
      return user;
    },
    coupons() {
      let coupons;
      this.$bus.$on('son_get_data', (data) => {
        coupons = data.coupons;
      });
      this.$bus.$emit('son_call_data');
      return coupons;
    },
  },
  data() {
    return {
      test: false,

      newOrder: {
        id: '',
        date: '',
        sum: '',
        cart: {
          sum: 0,
          products: [],
        },
        coupon: {
          id: '',
          description: '',
          dMethod: '',
          dIndex: '',
          discount: 0,
        },
        member: {},
        buyer: {
          name: '',
          tel: '',
          email: '',
        },
        invoice: {
          type: '',
          address: {},
        },
        recipient: {
          name: '',
          tel: '',
          address: {},
        },
        delivery: {
          get_from_store: false,
          anonymous: false,
          esdDate: '',
          esdTime: '',
          has_recipient2: false,
          recipient2Name: '',
        },
        others: {
          cardContent: '',
          remark: '',
        },
        payment: {
          status: 'notYet',
          type: '',
          creditNum: '',
          creditExp: {
            m: '',
            y: '',
          },
          cvc: '',
        },
      },

      cartContentConfirmed: false,

      // collapse status
      loginCollapse: false,
      cartConfirmingCollapse: true,
      cartConfirmedCollapse: true,
      sumCollapse: true,
      orderInfoCollapse: false,
    };
  },
  watch: {
    /* eslint-disable */
    'user.id': function () {
      this.newOrder.buyer.name = this.user.name;
      this.newOrder.buyer.tel = this.user.tel;
      this.newOrder.buyer.email = this.user.email;
    },
    /* eslint-enable */
  },
  methods: {
    go_page(route) {
      this.$router.push(route);
    },
    scroll_to_login() {
      const nodeTop = $('#checkout__login').offset().top;
      this.loginCollapse = true;
      $('html').animate({
        scrollTop: nodeTop - 150,
      });
    },
    confirm_cartContent() {
      this.cartContentConfirmed = true;
      this.clear_coupon();
      this.sum_newOrder();
    },
    cancel_confirm_cartContent() {
      this.cartContentConfirmed = false;
      this.clear_coupon();
    },
    clear_coupon() {
      this.newOrder.coupon = {
        id: '',
        description: '',
        discount_method: '',
        discount_index: 0,
        discount: 0,
      };
    },
    find_coupon() {
      const { id } = this.newOrder.coupon;
      const theCoupon = this.coupons.find((item) => item.id === id);
      /* eslint-disable */
      const calculateCouponDiscount = (method, index) => {
        switch (method) {
          case 'minus':
            return index;
            break;
          case 'times':
            return Math.floor((1 - index) * this.cart.sum);
            break;
          default:
        }
      };
      /* eslint-enable */
      if (theCoupon === undefined) {
        alert('無此優惠碼');
        this.clear_coupon();
      } else {
        const { description } = theCoupon;
        const dMethod = theCoupon.discount_method;
        const dIndex = theCoupon.discount_index;
        const discount = calculateCouponDiscount(dMethod, dIndex);

        this.newOrder.coupon.description = description;
        this.newOrder.coupon.dMethod = dMethod;
        this.newOrder.coupon.dIndex = dIndex;
        this.newOrder.coupon.discount = discount;
      }
      this.sum_newOrder();
    },
    sum_newOrder() {
      const cartP = this.cart.sum;
      const discountP = this.newOrder.coupon.discount;
      this.newOrder.sum = cartP - discountP;
    },
    get_address(targetObj, address) {
      this.$set(targetObj, 'address', address);
    },
    emit_order() {
      this.newOrder.id = (new Date()).msCode();
      this.newOrder.date = (new Date()).yyyymmdd();
      this.newOrder.member = this.user;
      this.newOrder.cart = this.cart;
      this.$bus.$emit('add_new_order', this.newOrder);
      alert(`訂單建立完成，您的訂單編號為 ${this.newOrder.id}`);
      this.orderInfoCollapse = false;
      this.go_page(`/front/checkout_payment/${this.newOrder.id}`);
    },
  },
  created() {
    if (this.user.id !== '') {
      this.newOrder.buyer.name = this.user.name;
      this.newOrder.buyer.tel = this.user.tel;
      this.newOrder.buyer.email = this.user.email;
    }
    this.$bus.$emit('toggleCart', false);
  },
};
</script>
